import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/components/Layout.vue'
import NotFound from '@/404.vue'
import Cookies from 'js-cookie'

Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/',
  //   name: 'HelloWorld',
  //   component: HelloWorld
  // },
  {
    path: '/login',
    component: () => import('@/views/login/index.vue')
  },
  {
    path: '/',
    component: Layout,
    children: [{
      path: '/registration',
      name: 'Registration',
      component: () => import('@/views/registration/Registration.vue')
    }]
  },
  {
    path: '/',
    component: Layout,
    redirect: '/helloWorld',
    name: 'HelloWorld',
    hidden: true, //layout을 보이지 않게 한다, 자식만 보이게 한다.
    children: [{
      path: '/helloWorld',
      component: () => import('@/views/HelloWorld')
    }]
  },
  {
    path: '/notice',
    component: Layout,
    children: [
      {
        path: 'list',
        name: 'NoticeList',
        component: () => import('@/views/notice/NoticeList.vue'),
        beforeEnter: (to, from, next) => {
        if(Cookies.get('token')) { //로그인이 유무
          if(to.path === '/login') { //로그인 됨 && 로그인 페이지?
            next({path: '/'})  // 로그인 됨 && 로그인 페이지 맞음
          } else { // 로그인 됨 && 로그인 페이지 아님
            next() //그럼 그 페이지에 머무르기
          }
        } else { //로그인이 안됨
          if(to.path !== '/login') { //로그인 안됨 && 로그인 페이지가 아님?
            next({path: '/login'}) //로그인 안됨 && 로그인 페이지가 아님
          } else { //로그인 안됨 && 로그인 페이지가 맞음
            next() //로그인 페이지
          }
        }
      }
      },
      {
        hidden: true,
        path: 'register',
        name: 'NoticeRegister',
        component: () => import('@/views/notice/NoticeRegister.vue')
      },
      {
        hidden: true,
        path: 'detail',
        name: 'NoticeDetail',
        component: () => import('@/views/notice/NoticeDetail.vue')
      }
    ]
  },
  
  {
    path: '/popup',
    component: Layout,
    children: [
      {
        path: 'register',
        name: 'PopupRegister',
        component: () => import('@/views/popup/PopupRegister.vue')
      }
    ]
  },
  // {
  //   path: '/noticeList',
  //   name: 'NoticeList',
  //   component: NoticeList
  // },
  // {
  //   path: '/noticeRegister',
  //   name: 'NoticeRegister',
  //   component: NoticeRegister
  // },
  // {
  //   path: '/noticeDetail',
  //   name: 'NoticeDetail',
  //   component: NoticeDetail
  // },
  // {
  //   path: '/popupRegister',
  //   name: 'PopupRegister',
  //   component: PopupRegister
  // },
  {
    path: '*', //위에 모든 라우터를 읽고나서도 없다면 404페이지를 보여준다.
    component: NotFound
  },
]

export default new VueRouter({
  routes
})



